<script>
//   import { mapState } from 'vuex'
  import Vue from 'vue'
  export default {
    name: 'Evaluaciones',
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
      MessageRibbon: () => import('@/components/web/base/MessageRibbon'),
      Splitter: () => import('@/components/web/base/Splitter'),
      SplitterVolver: () => import('@/components/web/base/SplitterVolver'),
    },
    data () {
      return {
        evaluaciones: [],
      }
    },
    created () {
      this.getEvaluaciones()
    },
    methods: {
      getEvaluaciones () {
        Vue.axios
          .get('/api/servicios/evaluaciones')
          .then(res => {
            this.evaluaciones = res.data.result
          })
          .catch(err => {
            console.log(err)
          })
      },
    },
    metaInfo: {
      title: 'Evaluaciones',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
